import React from "react";

function Header(props) {
  return (
    <div className="row block center">
      <div>
        <h1>My Shopping Cart</h1>
      </div>
      <div>
        <a href="#orderSummary">
          Cart{" "}
          {props.countCartItems ? (
            <button className="badge">{props.countCartItems}</button>
          ) : (
            ""
          )}
        </a>
      </div>
    </div>
  );
}

export default Header;
