import React from "react";
import ProductItem from "./ProductItem";
let orderArray = [];

function ProductDisplay(props) {
  const products = props.products;
  const productItems = products.map((data) => (
    <ProductItem key={data.id} item={data} onAdd={props.onAdd} />
  ));

  return (
    <div className="block">
      <h2>Products</h2>
      <div className="row productDisplay">{productItems}</div>
    </div>
  );
}

