import React from "react";

function ProductItem(props) {
  const product = props.item;
  console.log(product);
  return (
    <div className="col-2">
      <hr />
      <div className="row">
        <img
          src={product.productimage}
          className={"productimage"}
          alt="Product Image"
        />
        <h3 className="productText">{product.productname}</h3>
      </div>
      <h3>Price: ${product.price}</h3>
      <button onClick={() => props.onAdd(product)}>Add to Cart</button>
      <hr />
    </div>
  );
}

export default ProductItem;
