import React from "react";
import OrderDetail from "./OrderDetail";
import { orderArray } from "./ProductDisplay";

function OrderSummary(props) {
  const { cartItems } = props;
  const itemPrice = cartItems.reduce((a, c) => a + c.price * c.qty, 0);
  return (
    <div className="block" id="orderSummary">
      <h2>Order Summary</h2>
      <div>{cartItems.length === 0 && <div>Order Item is Empty</div>}</div>
      {cartItems.map((item) => (
        <div key={item.id} className="row">
          <div className="col-2">{item.productname}</div>
          <div className="col-1">
            <button onClick={() => props.onAdd(item)} className="add">
              +
            </button>
            <button onClick={() => props.onRemove(item)} className="remove">
              -
            </button>
          </div>
          <div className="col-1 text-right">
            {item.qty} x ${item.price.toFixed(2)}
          </div>
        </div>
      ))}
      {cartItems.length !== 0 && (
        <>
          <hr></hr>
          <div className="row">
            <div className="text-right">
              <strong></strong>{" "}
            </div>
            <div className="text-right">
              <strong>Total Price: </strong>
              {"   "}
              <strong>${itemPrice.toFixed(2)}</strong>
            </div>
          </div>
        </>
      )}
    </div>
  );
}

export default OrderSummary;
